<style lang="less">
@import '../../less/lib/mixins.less';
.page-user-setting{
    .block{
        background:#fff;
        
        .rem(padding, 26, 16);
        .rem(margin-bottom, 20);
        
        .clearfix{
            border-bottom:2px solid #dedede;
            .rem(border-bottom-width, 2);
            .rem(padding, 20, 0);
            .rem(font-size, 30);
            .rem(line-height, 40);            
            
            &:first-child{
                .rem(padding-top, 0);
            }
            
            &:last-child{
                border-bottom:0 none;
                .rem(padding-bottom, 0);
            }
            
            input{
                border:0 none;
                text-align:right;
            }
        }
    }
    
    .photoName{
        .rem(height, 120);
        .rem(line-height, 120); 
    }
    
    .photoWrap{
        .rem(width, 120);
        .rem(height, 120);
        .border-radius(120);
        img{
            width:100%;
            height:100%;
            .border-radius(120);
        }
    }
}
</style>

<template>
    <div class="page-user page-user-setting" transition="page">
    
        <header-bar :title="title" back="true"></header-bar>
        
        <div class="content showHeader showFooter">
            
            <div class="block">
                <div class="clearfix">
                    <div class="pull-left photoName"></div>
                    <div class="pull-right photoWrap">
                        <img :src="formData.head_img_url">
                    </div>
                </div>
                <div class="clearfix">
                    <div class="pull-left"> </div>
                    <div class="pull-right">
                        <input type="text" v-model="formData.nickname" />
                    </div>
                </div>
                <div class="clearfix">
                    <div class="pull-left"></div>
                    <div class="pull-right">{{formData.sex}}</div>
                </div>
                <div class="clearfix">
                    <div class="pull-left"></div>
                    <div class="pull-right">
                        <input type="text" id="datetime-picker" v-model="formData.birthday" mobiscroll-datetime="settings" />
                    </div>
                </div>
                <div class="clearfix">
                    <div class="pull-left"> </div>
                    <div class="pull-right">
                        <input type="text" v-model="formData.height" />
                    </div>
                </div>
            </div>
            
            <div class="block">
                <bind-mobile :mobile.sync="formData.mobile"></bind-mobile>
                <div class="clearfix">
                    <div class="pull-left"></div>
                    <div class="pull-right">
                        <input type="text" v-model="formData.email" />
                    </div>
                </div>
                <div class="clearfix">
                    <div class="pull-left">QQ</div>
                    <div class="pull-right">
                        <input type="text" v-model="formData.qq" />
                    </div>
                </div>
            </div>
            
            <div class="block">
                <div class="clearfix">
                    <div class="pull-left"></div>
                    <div class="pull-right">
                        <label class="label-switch">
                            <input type="checkbox" v-model="formData.open" >
                            <div class="checkbox"></div>
                        </label>
                    </div>
                </div>
                <time-conf :timer.sync="formData.timeConf"></time-conf>
            </div>
            
        </div>
        <span 
            class="ui-btn ui-btn-big"
            @click="save"
        >
            
        </span>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                title: '',
                tmpData: {},
                formData: {}
            }
        },
        ready(){
            let self = this;
            
            $.ajax({
                url: "/soytime/account/getSettingInfo",
                type:'POST',
                dataType: 'json',
                success: ((data)=>{
                    self.formData = data.result;
                    self.$store.state.getScrollerTime('#datetime-picker', 0);
                })
            });
        },
        methods:{
            save(){
                let self = this;
                //$.showPreloader('...')
                $.ajax({
                    url: "/soytime/account/saveSetInfo",
                    type:'POST',
                    dataType: 'json',
                    data: self.formData,
                    success: (data)=>{
                        if( data.success ){
                            //$.hidePreloader();
                            self.$route.router.go('/user');
                        }
                    },
                    error: ()=>{
                        //$.hidePreloader();
                       // $.toast('');
                    }
                });
            }
        },
        components: {
            'headerBar': require('../../components/header.vue'),
            'timeConf': require('../../components/timeConf.vue'),
            'bindMobile': require('../../components/bindMobile.vue')
        }
    }
</script>


