<style lang="less">
@import '../../less/lib/mixins.less';
.page-user-money{
    .moneyHeader{
        .rem(height, 400);
        background:url(../../img/moneyBg.jpg) no-repeat;
        background-size:100% 100%;
        position:relative;
        
        header{
            text-align:center;
            color:#fff;
            .rem(padding, 110, 0, 0);
            span{
                position:relative;
                .rem(font-size, 70);
            }
            i{
                position:absolute;
                left:0;
                font-style: normal;
                .rem(top, -30);
                .rem(font-size, 20);
            }
        }
        
        footer{
            position:absolute;
            bottom:0;
            left:0;
            width:100%;

            span{
                position:relative;
                z-index:1;
                float:left;
                width:50%;
                text-align:center;
                .rem(padding, 30);
                .rem(font-size, 34);
                .rem(margin, 20, 0);
                &:last-child{
                    border-left:1px solid #dedede;
                }
                &:first-child{
                    border-right:1px solid #dedede;
                }
            }
            
            div{
                background:#fff;
                position:absolute;
                left:0;
                top:0;
                height:100%;
                width:100%;
                .opacity(0.5);
                z-index:0;
            }
        }
    }
    .moneyList{
        background:#fff;
        
        header{
            background:#fafafa;
            border-bottom:2px solid #f1f1f1;
            .rem(border-bottom-width, 2);
            .rem(height, 64);
            .rem(line-height, 64);
            .rem(font-size, 30);
            .rem(padding, 0, 30);
        }
        
        .pull-refresh{
            .rem(margin, 0, 30);
            color:#888;
        }
        
        .item{
            border-bottom:2px solid #dedede;
            .rem(border-bottom-width, 2);
            .rem(padding, 15, 0);
        }
    }
}
</style>

<template>
    <div class="page-user page-user-money pullRreshwrap" transition="page">
        <header-bar :title="title" back="true"></header-bar>
        <div class="content showHeaderNopading">
            <div class="moneyHeader">
                <header>
                    <span><i></i>{{formData.balance}}</span>
                </header>
                <footer class="clearfix">
                    <div></div>
                    <span></span>
                    <span></span>
                </footer>
            </div>
            <div class="moneyList">
                <header></header>
                <pull-refresh @on-scroll-lodding="getData">
                    <li class="item clearfix" v-for="tradeRecord in formData.tradeRecord">
                        {{typeText[ tradeRecord.type - 1]}}
                        {{tradeRecord.create_time}}
                        {{tradeRecord.amount}}
                    </li>
                </pull-refresh>
            </div>
        </div>
    </div>
</template>

<script>
export default {
	data() {
		return {
			title: '',
			userInfo: {},
            currentPage: 1,
            typeText: ['','', '', '', '','', ''],
			formData: {
                balance: 0,
                tradeRecord: []
            }
		}
	},
	route: {
		data (transition){
			let self = this;
				
            $.ajax({
                url: "/soytime/account/balance",
                type:'POST',
                dataType: 'json',
                success: (data)=>{
                    $.extend(self.formData, data.result);
                },
                error: ()=>{
                    $.toast('');
                }
            });

		}
	},
    methods:{
        getData(index, callback){
            let self = this;
            $.ajax({
                url: "/soytime/account/tradeRecord",
                type:'POST',
                data:{
                    currentPage: index
                },
                dataType: 'json',
                success: ((data)=>{
                    let arr = data.result,
                            len = arr.length;
                    for(let i = 0; i<len; i++){
                        self.formData.tradeRecord.push(arr[i]);
                    }
                    //self.formData = self.formData.concat(data.result);
                    callback && callback();
                })
            });
        }
    },
	components: {
        'headerBar': require('../../components/header.vue'),
        'pullRefresh': require('../../components/pullRefresh.vue')
	}
}
</script>












