<style lang="less">
    @import '../../less/lib/mixins.less';
    .page-scene-orderSuccess{
        .content{
            padding-left: 0.65rem;
            padding-right: 0.65rem;
        }
        .header{
            text-align: center;
            color: #11cd6e;
            .rem(font-size, 34);
            .rem(padding-top, 10);
        }
        .subHeader{
            text-align: center;
            color:#b2b2b2;
            .rem(font-size, 24);
        }
        .jump{
            position: absolute;
            color:#b2b2b2;
            .rem(right, 10);
            .rem(top, 10);
            .rem(font-size, 24);
        }

        .imgWrap{
            .rem(padding, 10);
            img{
                height:100%;
                width:100%;
            }
        }

        .text{
            text-align: center;
            color:#b2b2b2;
            .rem(font-size, 24);

            p{
            .rem(padding, 5);
                margin:0;
            }
        }

        .userList{

            .list{
                .rem(padding, 10, 0);
            }
                .item{
                    border-bottom:1px solid #dedede;
                    .rem(border-bottom-width, 2);
                    .rem(padding, 6, 0);
                }
                header{
                    .rem(font-size, 24);
                }
                span{
                    display: inline-block;
                    .rem(height, 66);
                    .rem(line-height, 66);
                }
                .ico-xuan{
                    .rem(font-size, 40);
                    float: left;
                    color:#b2b2b2;
                    &.cur{
                        color:#11cd6e;
                     }
                }
                .img{
                    float: left;
                    .rem(width, 66);
                    .border-radius(66);
                    .rem(margin, 0, 20);
                    img{
                        height:100%;
                        width:100%;
                    }
                }
                .name{
                    float: left;
                    .rem(font-size, 24);
                }
                .ico-dianhua3{
                    .rem(font-size, 60);
                    float: right;
                    color: #54c5ff;
                }
        }

        .showOrderDetail{
            text-align: center;
            font-size: .85rem;
            height: 2.4rem;
            line-height: 2.3rem;
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            color:#11cd6e;
            background: #fafafa;
            //border-top:1px solid #dedede;
            //.rem(border-top-width, 2);
        }
    }

</style>

<template>
    <div transition="page" class="page-scene-orderSuccess page-current">
        <div class="content  showFooter">
            <div class="header">!</div>
            <div class="subHeader">2</div>
            <span class="jump"></span>
            <div class="imgWrap">
                <img src="/dist/img/orderSucess.png">
            </div>
            <div class="userList">
                <div class="text">
                    <p> 203 </p>
                    <p>3</p>
                </div>
                <div class="list">
                    <header>2</header>
                    <div class="item clearfix">
                        <span class="ico ico-xuan"></span>
                        <span class="img"><img src="/dist/img/orderSucess.png" alt=""></span>
                        <span class="name"></span>
                        <span class="ico ico-dianhua3" @click="getPhone(formData.name)"></span>
                    </div>
                </div>
            </div>
            <!--
            <div class="text">
                <p>
                     203 ...
                </p>
                <p>
                    0
                </p>
                <p>
                    30
                </p>
            </div>
            -->
        </div>
        <span class="showOrderDetail">
            
        </span>
    </div>

    <confirm :show.sync="isShowConfirm" @on-confirm="confirm">
        <div class="page-scene-orderSuccess-formWrap">
            {{confirmName}}
        </div>
    </confirm>
</template>

<script>
    export default {
        data(){
        return {
            title: null,
            isShowConfirm: false,
            confirmName: '',
            formData: {
                name: 'xxx'
            }
        }
    },
    route:{
        data (transition){
            let self  = this,
                query = transition.to.query;

            self.title = query.scene_name;
        }
    },
    ready: function () {

    },
    methods:{
        confirm(){
            self.isShowConfirm = false;
        },
        getPhone(name){
            let self = this;
            self.isShowConfirm = true;
            self.confirmName   = name;
        }
    },
    components: {
        'headerBar': require('../../components/header.vue'),
        'timeConf': require('../../components/timeConf.vue'),
        'confirm': require('../../components/confirm')
    }
    }
</script>



