<style lang="less">
@import '../../../less/lib/mixins.less';
.page-selectMap{
    .amap-toolbar{
        bottom:180px !important;
    }
    
    #panel {
        position: fixed;
        background-color: white;
        max-height: 90%;
        overflow-y: auto;
        left: 0;
        bottom: 0;
        width: 100%;
        border-bottom: solid 1px silver;
        z-index:200;
        display:table;
        background:#54C5FF;
        
        span{
            display: table-cell;
            vertical-align: middle;
            .rem(padding, 20);
            .rem(font-size, 24);
        }
        
        .mapText{
            width:80%;
			background:#fff;
        }
        .mapBtn{
            width:20%;
            text-align:center;
			color:#fff;
        }
    }

    .amap-geo{
        position: absolute;
        left: 0;
        bottom: 90px;
    }
}
</style>

<template>
    <div transition="page" class="page-selectMap page-current">
        <header-bar :title="title" :back="true"></header-bar>
        <div id="amapWrap" class="content"></div>
		<div id="panel"></div>
    </div>
</template>
<script>
export default  {
    data(){
        return {
            title:'',
            formData: {
                latitude: '',
                longitude: '',
                workplace: '',
                city_id: '',
                school_id: ''
            }
        }
    },
    route: {
        data (transition){
            let self     = this,
                query    = transition.to.query;
                
            $.extend(self.formData, query);
        }
    },
    ready(){

        if( !window.AMap ){
            setTimeout(()=>{
                location.reload(); 
            }, 15000);
            return;
        }
        
        let self        = this,
            map         = new AMap.Map('amapWrap', {
                resizeEnable: true,
                zoom:14
            }),
            geolocation = null,
            geocoder    = null,
            marker      = null,
            placeSearch = null,
            toAds       = (lnglatXY)=>{
                geocoder.getAddress(lnglatXY, function(status, result) {
                    if (status === 'complete' && result.info === 'OK') {
                        let ads   = result.regeocode.formattedAddress,
                            panel = $('#panel').html('<span class="mapText">'+ ads +'</span><span class="mapBtn"></span>');
                                    
                        panel.on('click', '.mapBtn', function(e){
                            self.formData.workplace = ads;
                            self.formData.latitude  = lnglatXY[0];
                            self.formData.longitude = lnglatXY[1];
                            self.$route.router.go('/auth/step3?' + $.param( self.formData ) );
                            return false;
                        });
                    }
                });  
            };
        
        map.plugin(["AMap.ToolBar"], function() {
            map.addControl(new AMap.ToolBar());
        });

        map.plugin('AMap.Geolocation', function () {
            geolocation = new AMap.Geolocation({
                enableHighAccuracy: true,//:true
                timeout: 10000,          //10
                maximumAge: 0,           //00
                convert: true,           //true
                showButton: true,        //true
                buttonPosition: 'LB',    //'LB'
                buttonOffset: new AMap.Pixel(10, 20),//Pixel(10, 20)
                showMarker: true,        //true
                showCircle: false,        //true
                panToLocation: true,     //true
                zoomToAccuracy:false      //false
            });
            map.addControl(geolocation);
        });
        
        map.plugin('AMap.Geocoder', ()=>{
                geocoder = new AMap.Geocoder({
                radius: 1000,
                extensions: "all"
            });
        });
        
        /*  */
        if( geolocation && geocoder ){
            /*  */
            geolocation.getCurrentPosition();
            
            /*  */
            AMap.event.addListener(geolocation, 'complete', (data)=>{
                toAds( [data.position.getLng(), data.position.getLat()] );
                map.on('click', function(e) {
                    let position  = [e.lnglat.getLng(), e.lnglat.getLat()];
                        
                    if( marker ){
                        marker.setPosition(position);
                        toAds(position);
                    }else{
                        marker = new AMap.Marker({
                            icon: "http://webapi.amap.com/theme/v1.3/markers/n/mark_b.png",
                            position: position
                        });
                        marker.setMap(map);
                        toAds(position);
                    }
                });
            });
        }else{
            setTimeout(()=>{
                location.reload(); 
            }, 15000);
        }
    },
    components: {
        'headerBar': require('../../../components/header.vue')
    }
}
</script>


